var FooterPro = React.createClass({
    clickFun:function(){
        // if($(".cartNum").html()){

        // }
            var cartNum = localStorage.getItem("cartNum");
        $(".cartNum").html(cartNum);
        
    },
    componentDidMount:function(){
        var cartNum = localStorage.getItem("cartNum");
        if(!cartNum){
            $(".cartNum").html(0);
        }else{
            $(".cartNum").html(cartNum);
        }

    },
    render:function(){
        return (
            <div className="footerPro">
                <div className="kefu">
                    <i className="iconfont icon-kefu"></i><span>客服</span>
                </div>
                <a href="#home" className="shouye">
                    <i className="iconfont icon-f-home"></i><span>首页</span>
                </a>
                <a href="#cart" className="cart">
                    <i className="iconfont icon-f-cart"></i><span className="cartNum">0</span>
                </a>
                <button onClick={this.clickFun}>加入购物车</button>
            </div>
        )
    }
})
module.exports = FooterPro;


