
var hwTools = require("../json/hwTools.js");


var HouseworkContainer = React.createClass({
    getInitialState:function(){
        return {
            newList:hwTools
        }
    },
    render:function(){
        var list = this.state.newList;
        var domList = list.map(function(item,index){
            return <HouseItem key={index} src={item.imgSrc} text={item.goodsName} price={item.price} comment={item.comment}
                        label={item.label} goodsId={index+1}/>
        })
        return (
            <div className="houseworkContainer">
                <div className="house-btn">
                    <a>新品</a><a>畅销</a><a>价格</a>
                </div>
                <ul>
                    {domList}
                </ul>
            </div>
        )
    }
})

var HouseItem = React.createClass({
    render:function(){
        // var h = null;
        // if(!this.props.tag){
        //     h = "tag";
        // }
        return (
            <li>
                <a href={"#productIntroduce?goodsId="+this.props.goodsId}>
                    <img src={this.props.src} />
                    <h5>{this.props.text}</h5>
                    <p>
                        {/*<e className={h}>{this.props.tag}</e><span className="strong">￥<i>{this.props.price}</i></span><del>{this.props.oldPrice}</del><b>{this.props.label}</b><span className="comment">评论： {this.props.comment}</span>*/}
                        <span className="strong">￥<i>{this.props.price}</i></span><span className="comment">评论： {this.props.comment}</span>
                        
                    </p>
                </a>
            </li>
        )
    }
})

module.exports = HouseworkContainer;


