
var curpage1 = require("../json/curpage1.js");
var curpage2 = require("../json/curpage2.js");

var StrollContainer = React.createClass({
    getInitialState: function () {
        return {
            strollList1: curpage2["InnerData"].StrollList,
            strollList2: curpage1["InnerData"].StrollList,
        }
    },

    render: function () {
        var list = this.state.strollList1;
        //console.log(list);
        var domlist = list.map(function (item, index) {
            if (item.ActivityPrice == 0) {
                return <StrollItem1 key={index} src={"http://i.lifevccdn.com" + item.ImageUrl}
                    text={item.Name} price={item.SalePrice}
                    count={item.SaleQty} />
            } else {
                return <StrollItem1 key={index} src={"http://i.lifevccdn.com" + item.ImageUrl}
                    text={item.Name} price={item.ActivityPrice} oldPrice={item.SalePrice}
                    count={item.SaleQty} />
            }
        })

        var list2 = this.state.strollList2;
        var linkDoms = null;
        //console.log(list);
        var domlist2 = list2.map(function (item, index) {
            if (item.Type == 1) {
                if (item.ActivityPrice == 0) {
                    return <StrollItem2 key={index} src={"http://i.lifevccdn.com" + item.ImageUrl}
                        text={item.Name} price={item.SalePrice}
                        count={item.SaleQty} />
                } else {
                    return <StrollItem2 key={index} src={"http://i.lifevccdn.com" + item.ImageUrl}
                        text={item.Name} price={item.ActivityPrice} oldPrice={item.SalePrice}
                        count={item.SaleQty} />
                }
            }
            if (item.Type == 3) {
                var links = item.ConfigerList;
                linkDoms = links.map(function (obj, index) {
                    return <StrollLink key={index} href={obj.LinkUrl} text={obj.Title} />
                });
            }
        })


        return (
            <div className="strollContainer">
                <div className="stroll-left">
                    <ul>
                        {domlist}
                    </ul>
                </div>
                <div className="stroll-right">
                    <div className="stroll-link">
                        {linkDoms}
                    </div>
                    <ul>
                        {domlist2}
                    </ul>
                </div>
            </div>
        )
    }
})

var StrollItem1 = React.createClass({
    render: function () {
        return (
            <li>
                <a href="javascript:;">
                    <img src={this.props.src} />
                    <h5>{this.props.text}</h5>
                    <p>
                        <span className="red">￥<i>{this.props.price}</i></span><del>{this.props.oldPrice}</del><span className="count">月销 {this.props.count}</span>
                    </p>
                </a>
            </li>
        )
    }
})
var StrollItem2 = React.createClass({
    render: function () {
        return (
            <li>
                <a href="javascript:;">
                    <img src={this.props.src} />
                    <h5>{this.props.text}</h5>
                    <p>
                        <span className="red">￥<i>{this.props.price}</i></span><del>{this.props.oldPrice}</del><span className="count">月销 {this.props.count}</span>
                    </p>
                </a>
            </li>
        )
    }
})

var StrollLink = React.createClass({
    render: function () {
        return (
            <a href={this.props.href}>{this.props.text}</a>
        )
    }
})
module.exports = StrollContainer;


