var Rlheader = require("../component/rl-header.js");
var RegisterContainer = require("../component/registerContainer.js");

var Register = React.createClass({
    render:function(){
        return (
            <div>
                <Rlheader />
                <RegisterContainer />
            </div>
        )
    }
})

module.exports = Register;






