
var Swiper = require("swiper");

require("swiper/dist/css/swiper.css");

var Banner = React.createClass({
    componentDidMount: function () {
        var swiper = new Swiper("." + this.props.bannerName, {
            pagination: ".swiper-pagination",
            loop: true,
            autoplay: 2000,
            autoplayDisableOnInteraction: false,

        })
    },
    render: function () {
        var obj = this.props.bannerList;

        return (
            <div className={"swiper-container " + this.props.bannerName}>
                <div className="swiper-wrapper">
                    <div className="swiper-slide">
                        <a href="javascript:;">
                            <img src={obj.imgSrc1} />
                        </a>
                    </div>
                    <div className="swiper-slide">
                        <a href="javascript:;">
                            <img src={obj.imgSrc2} />
                        </a>
                    </div>
                    <div className="swiper-slide">
                        <a href="javascript:;">
                            <img src={obj.imgSrc3} />
                        </a>
                    </div>
                    <div className="swiper-slide">
                        <a href="javascript:;">
                            <img src={obj.imgSrc2} />
                        </a>
                    </div>
                    <div className="swiper-slide">
                        <a href="javascript:;">
                            <img src={obj.imgSrc3} />
                        </a>
                    </div>
                    <div className="swiper-slide">
                        <a href="javascript:;">
                            <img src={obj.imgSrc4} />
                        </a>
                    </div>
                    <div className="swiper-slide">
                        <a href="javascript:;">
                            <img src={obj.imgSrc5} />
                        </a>
                    </div>
                </div>
                <div className="swiper-pagination"></div>
            </div>
        )
    }
})

module.exports = Banner;


