var Register = React.createClass({
    getUpdate:function(){
        $("#update-img").attr("src","http://account.lifevc.com/Account/NewVerifyCode?t=1499310484644?rnd=" + Math.random());
    },
    regBtnFun:function(){
        var user = $("#regNum").val();
        var pwd = $("#regPwd").val();
        var pho = $("#regPho").val();
        var mobie = $("#regMobie").val();
        //console.log(user);
      
        if(!(/^1[34578]\d{9}$/.test(user))){ 
            $(".alertDiv").show().delay(3000).fadeOut();
            $(".alertDiv").html("请输入正确的手机号");
        }else if(!(/^[A-Za-z0-9]{6,20}$/).test(pwd)){
            $(".alertDiv").show().delay(3000).fadeOut();
            $(".alertDiv").html("请输入有效密码，6-20位密码，字母、数字或符号");
        }else if(pho == ""){
            $(".alertDiv").show().delay(3000).fadeOut();
            $(".alertDiv").html("请先输入图形验证码");
        }else if(mobie == ""){
            $(".alertDiv").show().delay(3000).fadeOut();
            $(".alertDiv").html("请先输入手机验证码");
        }else{
            $.ajax({
				url:"http://localhost:8080/register",
				type:"post",
				data:{
					user:user,
					pwd:pwd
				},
				success:function(data){
					console.log(data)				
				}
			})
			localStorage.setItem("user",user) ;
			window.location.href="#/usercenter";	
        }
    },
    render:function(){
        return (
            <div className="register-main">
                <h1>新用户注册</h1>
                <ul>
                    <li><input type="tel" id="regNum" className="ipt" placeholder="请输入手机号" required="required" maxLength="11"/></li>
                    <li><input type="text" id="regPwd" className="ipt" placeholder="请设置6-20位密码,包含字母、数字或符号" required="required" maxLength="20"/></li>
                    <li><input type="text" id="regPho" className="ipt" placeholder="请输入图形验证码" required="required" maxLength="20"/>
                        <div className="verify">
                            <img src="http://account.lifevc.com/Account/NewVerifyCode?t=1499310484644" id="update-img"/>
                            <img src="img/update.png" id="update" onClick={this.getUpdate}/>
                        </div>
                    </li>
                    <li>
                        <input type="text" id="regMobie" className="ipt" placeholder="请输入手机验证码" required="required" maxLength="20"/>
                        <a>获取验证码噻</a>
                    </li>
                </ul>
                <p>遇到问题？请<a href="javascript:;">联系客服</a></p>
                <div className="btn-register"><a id="regBtn" onClick={this.regBtnFun}>注册</a></div>
                <div className="btn-login"><a href="#login">登录</a></div>
                <div className="alertDiv">输入</div>
            </div>
        )
    }
})

module.exports = Register;

