var Banner = require("./banner.js");
var bannerJson = require("../json/bannerJson.js");


var Shouye = React.createClass({
    getInitialState:function(){
        return {
            bannerList:bannerJson
        }
    },
    render:function(){
        // console.log(bannerList);
        return (
            <div className="main">
                <Banner bannerList = {this.state.bannerList}/>
                <div className="tab">
                    <div><a href="javascript:;"><img src="img/ww1.jpg"/></a></div>
                    <div><a href="javascript:;"><img src="img/ww2.jpg"/></a></div>
                    <div><a href="javascript:;"><img src="img/ww3.jpg"/></a></div>
                </div>
                <div className="newP">
                    <div className="newText">
                        <h4>新品首发</h4>
                        <p>天天有新品 每天来逛逛</p>
                    </div>
                    <div className="newPb">
                        <ul>
                            <li><a href="javascript:;"><img src="img/xinpin/n1.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/xinpin/n2.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/xinpin/n3.jpg"/></a></li>
                        </ul>
                    </div>
                    <div className="newPs">
                        <ul>
                            <li><a href="javascript:;"><img src="img/xinpin/nn1.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/xinpin/nn2.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/xinpin/nn3.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/xinpin/nn4.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/xinpin/nn5.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/xinpin/nn6.jpg"/></a></li>
                        </ul>
                    </div>
                </div>
                <div className="oldP">
                    <div className="oldPb"><a href="javascript:;"><img src="img/mb.jpg"/></a></div>
                    <p>老会员补货区</p>
                    <div className="oldPs">
                        <ul>
                            <li><a href="javascript:;"><img src="img/oldP/old1.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/oldP/old2.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/oldP/old3.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/oldP/old4.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/oldP/old5.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/oldP/old6.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/oldP/old7.jpg"/></a></li>
                            <li><a href="javascript:;"><img src="img/oldP/old8.jpg"/></a></li>
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
})

// var newPbItem = 
module.exports = Shouye;


