var Header = React.createClass({
    render: function () {
        var dom = "";
        var headerBtn = "";
        var headerBack = "";
        var headerSet = "";
        if(this.props.headerObj.type=="HEADER_P"){
            dom = <HeaderText title={this.props.headerObj.title}/>
        }else if(this.props.headerObj.type=="HEADER_DIV"){
            dom = <HeaderBj />
        }
        if(this.props.headerObj.isBtn){
            headerBtn = <HeaderBtn />
        }
        if(this.props.headerObj.isBack){
            headerBack = <HeaderBack />
        }
        if(this.props.headerObj.isSet){
            headerSet = <HeaderSet />
        }
        return (
            <header>
                {dom}
                {headerBtn}
                {headerBack}
                {headerSet}
            </header>
        )
    }
})

var HeaderBj = React.createClass({
    render:function(){
        return (
            <div className="head-bj">
                <img src="img/h-bj.png"/>
            </div>
        )
    }
})
var HeaderBtn = React.createClass({
    render:function(){
        return (
            <a href="#all" className="btn right iconfont icon-sousuo"></a>
        )
    }
})
var HeaderBack = React.createClass({
    render:function(){
        return (
            <a href="javascript:history.go(-1)" className="btn-back iconfont icon-back"></a>
        )
    }
})
var HeaderSet = React.createClass({
    render:function(){
        return (
            <a href="#set" className="btn-set">设置</a>
        )
    }
})
var HeaderText = React.createClass({
    render:function(){
        return (
            <div className="head-text">{this.props.title}</div>
        )
    }
})

module.exports = Header;


