var houseworkJson = require("../json/housework.js");
var cookJson = require("../json/cook.js");
var suitJson = require("../json/suit.js");
// var allOperate = require("../operate/allOperate.js");

var AllContainer = React.createClass({
    getInitialState:function(){
        return {
            HousworkList:houseworkJson,
            CookList:cookJson,
            SuitList:suitJson,
        }
    },
    clickHide:function(){
        $(".search p").hide();
        $(".btn-search").show();
    },
    clickShow:function(){
        $(".btn-search").hide();
        $(".search p").show();
    },
    render:function(){
        var hslist = this.state.HousworkList;
        var hsdomList = hslist.map(function(item,index){
            return <HouseworkItem key={index} i={item.goodsId} src={item.imgSrc} text={item.goodsName}/>
        })
        var cklist = this.state.CookList;
        var ckdomList = cklist.map(function(item,index){
            return <CookItem key={index} i={item.goodsId} src={item.imgSrc} text={item.goodsName}/>
        }) 
        var stlist = this.state.SuitList;
        var stdomList = stlist.map(function(item,index){
            return <SuitItem key={index} i={item.goodsId} src={item.imgSrc} text={item.goodsName}/>
        }) 
        return (
            <div className="allContanier">
                <div className="search">
                    <p onClick={this.clickHide}><i className="iconfont icon-search"></i> 搜索商品</p>
                    <div className="btn-search" onClick={this.clickShow}><i className="i-p iconfont icon-search"></i><input placeholder="搜索商品"/><a className="cancel">取消</a></div>
                </div>
                <div className="housework">
                    <h4>家务</h4>
                    <ul>
                        {hsdomList}
                    </ul>
                </div>
                <div className="cook">
                    <h4>下厨</h4>
                    <ul>
                        {ckdomList}
                    </ul>
                </div>
                <div className="suit">
                    <h4>家居服</h4>
                    <ul>
                        {stdomList}
                    </ul>
                </div>
            </div>
        )
    }
})

var HouseworkItem = React.createClass({
    render:function(){
        if(this.props.i==3 || this.props.i==6 || this.props.i==9 || this.props.i==11){
            var noMR = "noMR";
        }
        return (
            <li className={noMR}>
                <a href="#houseworkTools">
                    <div><img src={this.props.src} /></div>
                    <p>{this.props.text}</p>
                </a>
            </li>
        )
    }
});

var CookItem = React.createClass({
    render:function(){
        if(this.props.i==3 || this.props.i==6 || this.props.i==9 || this.props.i==11){
            var noMR = "noMR";
        }
        return (
            <li className={noMR}>
                <a href="javascript:;">
                    <div><img src={this.props.src} /></div>
                    <p>{this.props.text}</p>
                </a>
            </li>
        )
    }
});

var SuitItem = React.createClass({
    render:function(){
        if(this.props.i==3 || this.props.i==6 || this.props.i==9 || this.props.i==11){
            var noMR = "noMR";
        }
        return (
            <li className={noMR}>
                <a href="javascript:;">
                    <div><img src={this.props.src} /></div>
                    <p>{this.props.text}</p>
                </a>
            </li>
        )
    }
});

module.exports = AllContainer;


