import{Link} from "react-router";
var Nav = React.createClass({
    render: function () {
        var list = this.props.navList;
        var domList = list.map(function(item,index){
            if(index == 0){
                return <NavItem key={index} text={item.text} href={item.href} className="active"/>
            }else{
                return <NavItem key={index} text={item.text} href={item.href}/>
            }
        })
        var width = 1.60*domList.length;
        width = width + "rem";
        return (
            <nav>
                <ul style={{width}}>
                    {domList}
                </ul>
            </nav>
        )
    }
});
var NavItem = React.createClass({
    render:function(){
        var txt = "";
        if(this.props.className == "active")txt = "active";
        return (
            // <li className={txt}>{this.props.text}</li>
            <li>
                
                {/*<a href="javascript:;" onClick={this}></a>*/}
                <Link to={"home/"+this.props.href} activeClassName="active">
                    {this.props.text}
                </Link>
            </li>
        )
    }
})
module.exports = Nav;


