import{Route,Router,hashHistory,IndexRedirect} from "react-router"

var ReactDom = require("react-dom");
var app = document.getElementById("app");

require("../less/style.less");

//引入Home和List
var Home = require("./views/page/home.js");

var All = require("./views/page/all.js");
var ShouyeContent = require("./views/component/shouyeContent.js");
var NewContent = require("./views/component/newContent.js");

var Stroll = require("./views/page/stroll.js");
var Cart = require("./views/page/cart.js");
var UserCenter = require("./views/page/usercenter.js");

var Register = require("./views/page/register.js");
var Login= require("./views/page/login.js");
var Sett = require("./views/page/set.js");
var HouseworkTools = require("./views/page/housework.js");
var ProductIntroduce = require("./views/page/productIntroduce.js");

var Main = React.createClass({
    render:function(){
        return this.props.children
    }
});
var Page = React.createClass({
    render:function(){
        return (
            <Router history={hashHistory}>
                <Route path="/" component={Main}>
                    <IndexRedirect to={"home"} />
                    <Route path="home" component={Home}>
                        <IndexRedirect to={"shouye"} />
                        <Route path="shouye" component={ShouyeContent}></Route>
                        <Route path="new" component={NewContent}></Route>
                        <Route path="channel" component={NewContent}></Route>
                        <Route path="cook" component={NewContent}></Route>
                        <Route path="suit" component={NewContent}></Route>
                        <Route path="life" component={NewContent}></Route>
                        <Route path="soft" component={NewContent}></Route>
                        <Route path="bed" component={NewContent}></Route>
                        <Route path="work" component={NewContent}></Route>
                        <Route path="understanding" component={NewContent}></Route>
                    </Route>
                    <Route path="all" component={All}></Route>
                    <Route path="stroll" component={Stroll}></Route>
                    <Route path="cart" component={Cart}></Route>
                    <Route path="usercenter" component={UserCenter}></Route>
                    <Route path="register" component={Register}></Route>
                    <Route path="login" component={Login}></Route>
                    <Route path="set" component={Sett}></Route>
                    <Route path="houseworkTools" component={HouseworkTools}></Route>
                    <Route path="productIntroduce" component={ProductIntroduce}></Route>
                </Route>
            </Router>
        )
    }
})

ReactDom.render(<Page />,app);


