import{Link} from "react-router";
var Footer= React.createClass({
    render:function(){
        var list = this.props.footList;
        // console.log(list);
        var domList = list.map(function(item,index){
            return <FooterItem key={index} href={item.href} btnClass={item.className}  btnText={item.text}/>
        })
        return (
            <footer>
                <ul>
                    {domList}
                </ul>
            </footer>
        )
    }
})
var FooterItem = React.createClass({
    render:function(){
        return (
            <li >
                <Link to={this.props.href} activeClassName="active" >
                    <div className={"iconfont " + this.props.btnClass}></div>
                    <p>{this.props.btnText}</p>
                </Link>
             </li>
        )
    }
})

module.exports = Footer;


