var Header = require("../component/header.js");
var FooterPro = require("../component/footerPro.js");
var ProductContainer = require("../component/productContainer.js");

var ProductIntroduce = React.createClass({
    getInitialState:function(){
        return {
             headerObj:{
                type:"HEADER_P",
                isBtn:false,
                isBack:true,
                isSet:false,
                title:"商品介绍"
            }
        }
    },
    render:function(){
        return (
            <div>
                <Header headerObj={this.state.headerObj}/>
                <ProductContainer />
                <FooterPro />
            </div>
        )
    }
})
module.exports = ProductIntroduce;


