import { browserHistory } from 'react-router';
var url = require("url");

var BannerPro = require("./bannerPro.js");
var productJson = require("../json/product.js");

var ProductContainer = React.createClass({
    getInitialState: function () {
        return {
            bannerList: productJson,
            bannerName: "bannerPro",
            hidden: "hidden",
            show: "show",
            isChange:true,
            isBtn:true,
            search:window.location.href,
            // var search = browserHistory.getCurrentLocation().pathname;
            goodsObj:null,
            gtag:null,
            stateI:null
        }
    },
    componentWillMount:function(){
        var hash = url.parse(this.state.search,true,true).hash;
        var goodsId = hash.split("?")[1].split("&")[0].split("=")[1];
        // console.log(goodsId);
        // console.log(this.state.bannerList);
        for(var i = 0;i<this.state.bannerList.length;i++){
            if(goodsId == this.state.bannerList[i].goodsId){
                // console.log(this.state.bannerList[i]);
                this.setState({
                    goodsObj:this.state.bannerList[i]
                })
            }
        }
        
        if(localStorage.getItem("user")){
            this.setState({
                show:"hidden"
            })
        }
    },
    clickFun:function(e){
        var thisObj =e.target;
        $("li").removeClass("active");
        $(thisObj).addClass("active");
        
        this.state.gtag = $(thisObj).html();
        // console.log(this.state.gtag);
    },
    componentDidMount:function(){
        var h = $(document).find(".active")[0];
        console.log($(h).html());
        
        this.state.gtag = $(h).html();
        this.setState({
            gtag:this.state.gtag
        })
        console.log(this.state.gtag);


    },
    addFun:function(){
        this.state.isBtn = true;
        if(this.state.isBtn){
            var i = $(".count").html();
            i++;
            $(".count").html(i);
            localStorage.setItem("cartNum",i);
            
            // if(localStorage.getItem("cartNum")>1){
            //     var m = localStorage.getItem("cartNum")*1;
            //     console.log(m);
            //     localStorage.setItem("cartNum",i*1+m);
            // }

            //此处必须是点击加号才能把该商品存到本地
            var goodsId,goodsName,goodsSrc,goodsPrice,goodsTag,goodsNum;
            this.state.stateI = i;
            var productObj = {
                goodsId : this.state.goodsObj.goodsId,
                goodsName : this.state.goodsObj.goodsName,
                goodsSrc : this.state.goodsObj.imgSrc,
                goodsPrice : this.state.goodsObj.price,
                goodsTag : this.state.gtag,
                goodsNum : this.state.stateI,
            }
            var productStr = JSON.stringify(productObj);
            localStorage.setItem("cartPro_"+productObj.goodsName,productStr);
            
        }
    },
    lessFun:function(){
        var i = $(".count").html();
        if(i == 1){
            if(this.state.isBtn){
                this.state.isBtn = false;
            }
        }else{
            i--;
            $(".count").html(i);
            var now=0;
            if(localStorage.getItem("cartNum") >= 1){
                now = localStorage.getItem("cartNum");
                localStorage.setItem("cartNum",now-i);
            }
        }
    },
    render: function () {
        var proObj = this.state.goodsObj;
        // console.log(proObj);

        var t = "";
        if(this.state.isChange){
            t = "active";
        }else{
            t = "";
        }
        var that = this;
        var tags = proObj.tag;
        var tagList = tags.map(function(item,index){
            if(index == 0){
                var cl="active";
            }
            return <TagItem key={index} tag={item} click={that.clickFun} index={index} className={cl}/>
        })

        return (
            <div className="productContainer">
                <BannerPro bannerList={proObj} bannerName={this.state.bannerName}/>
                <div className="pro_main">
                    <div className="pro_main_text">
                        <h2>{proObj.goodsName}</h2>
                        <h6>{proObj.desc}</h6>
                        <h5>￥{proObj.price}</h5>
                        <p className={this.state.show + " isLogin"}>
                            <a href="#login"><i>优惠</i><span className="red">登录查看你的积分和优惠券</span></a>
                        </p>
                        <p className="shangpin">商品：</p>
                        <ul>
                            {tagList}
                        </ul>
                        <div className="countDiv">
                            <span>数量：</span><span><button onClick={this.lessFun}>-</button><span className="count">1</span><button onClick={this.addFun
                            }>+</button></span>
                        </div>
                        <div className="derDiv">
                            <p>送至：<span>湖北</span>（满￥149免运费）</p>
                            <p className="red">新会员首单，满69元免运费</p>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
})

var TagItem = React.createClass({

    render: function () {
        return (
            <li onClick={this.props.click} data-id={this.props.index} className={this.props.className}>
                {this.props.tag}
            </li>
        )
    }
})
module.exports = ProductContainer;


