import{Link} from "react-router";
var Rlheader = React.createClass({
    clickBack:function(){
        window.history.go(-1);
    },
    render:function(){
        return (
            <div className="rl-head">
                <img src="img/regist-bj.png"/>
                <Link onClick={this.clickBack}><i className="iconfont icon-back"></i></Link>
            </div>
        )
    }
})

module.exports = Rlheader;



