var Header = require("../component/header.js");
var SetContainer = require("../component/setContainer.js");

var Sett = React.createClass({
    getInitialState:function(){
        return {
            headerObj:{
                type:"HEADER_P",
                isBtn:false,
                isBack:true,
                isSet:false,
                title:"设置"
            },
        }
    },
    render:function(){
        return (
            <div>
                <Header headerObj={this.state.headerObj}/>
                <SetContainer />
            </div>
        )
    }
})

module.exports = Sett;


