
var newJson = require("../json/newProduct.js");


var Shouye = React.createClass({
    getInitialState:function(){
        return {
            newList:newJson
        }
    },
    render:function(){
        var list = this.state.newList;
        var domList = list.map(function(item,index){
            if(item.label.length>0){
                if(item.tag == null){
                    return <NewItem key={index} src={item.imgSrc} text={item.goodsName} price={item.price} comment={item.comment}
                        label={item.label}/>
                }else{
                    if(item.oldPrice){
                        return <NewItem key={index} src={item.imgSrc} text={item.goodsName} price={item.price} comment={item.comment}
                            label={item.label} tag={item.tag} oldPrice={item.oldPrice}/>
                    }
                }
            }
        })
        return (
            <div className="main">
                <div className="main-new">
                    <p className="newTitle">最近一周新品</p>
                    <ul>
                        {domList}
                    </ul>
                </div>
            </div>
        )
    }
})

var NewItem = React.createClass({
    render:function(){
        var h = null;
        if(!this.props.tag){
            h = "tag";
        }
        return (
            <li>
                <a href="javascript:;">
                    <img src={this.props.src} />
                    <h5>{this.props.text}</h5>
                    <p>
                        <e className={h}>{this.props.tag}</e><span className="strong">￥<i>{this.props.price}</i></span><del>{this.props.oldPrice}</del><b>{this.props.label}</b><span className="comment">评论： {this.props.comment}</span>
                    </p>
                </a>
            </li>
        )
    }
})

module.exports = Shouye;


