var Header = require("../component/header.js");
var HouseworkContainer = require("../component/houseworkContainer.js");

var Cart = React.createClass({
    getInitialState:function(){
        return {
             headerObj:{
                type:"HEADER_P",
                isBtn:false,
                isBack:true,
                isSet:false,
                title:"家务"
            }
        }
    },
    render:function(){
        return (
            <div>
                <Header headerObj={this.state.headerObj}/>
                <HouseworkContainer />
            </div>
        )
    }
})
module.exports = Cart;


