var Header = require("../component/header.js");
var Nav = require("../component/nav.js")
var Footer = require("../component/footer.js");

// var IndexContent = require("../component/indexContent.js");

var Home = React.createClass({

    getInitialState:function(){
        return {
            headerObj:{
                type:"HEADER_DIV",
                isBtn:true,
                title:""
            },
            navList:[
                {
                    text:"首页",
                    href:"shouye"
                },
                {
                    text:"新品",
                    href:"new"
                },
                {
                    text:"家务",
                    href:"channel"
                },
                {
                    text:"下厨",
                    href:"cook"
                },
                {
                    text:"家居服",
                    href:"suit"
                },
                {
                    text:"生活",
                    href:"life"
                },
                {
                    text:"软装",
                    href:"soft"
                },
                {
                    text:"床品",
                    href:"bed"
                },
                {
                    text:"工作和旅行",
                    href:"work"
                },
                {
                    text:"了解LifeVC",
                    href:"understanding"
                }
            ],
            footList:[
                {
                    className:"icon-f-home",
                    text:"首页",
                    href:"home"
                },
                {
                    className:"icon-f-all",
                    text:"全部产品",
                    href:"all"
                },
                {
                    className:"icon-f-xianguang",
                    text:"闲逛",
                    href:"stroll"
                },
                {
                    className:"icon-f-cart",
                    text:"购物车",
                    href:"cart"
                },
                {
                    className:"icon-f-account",
                    text:"账号中心",
                    href:"usercenter"
                }
            ]
        }
    },
    render:function(){
        return (
            <div>
                <Header headerObj={this.state.headerObj}/>
                <Nav navList={this.state.navList}/>
                {this.props.children}
                <Footer footList={this.state.footList}/>
            </div>
        )
    }
})

module.exports = Home;


